<template>
  <div>
    <div class="container">
      <div class="row m-0">
        <h2 class="title_border_button w-100 mt-5">{{ $t("login.title") }}</h2>
        <div class="col-md-6 pl-0 mt-5">
          <h3 class="mb-3">{{ $t("login.customers") }}</h3>
          <p>{{ $t("login.customers_text") }}</p>
          <router-link class="primary-btn" to="registered">{{ $t("login.create") }}</router-link>
          <br>
        </div>
        <div class="col-md-6 mt-4 p-0">
          <form class="border p-3 login" @submit.prevent="signin">
            <h3 class="mt-1">{{ $t("login.login") }}</h3>
            <div
              class="alert"
              v-if="res.status === false"
              :class="{'alert-danger':!res.status}"
            >{{$t("messages.sign_f")}}</div>
            <small>{{ $t("login.sm1") }}</small>
            <br>
            <small class="text-danger">*{{ $t("login.sm2") }}</small>
            <div class="form-group mt-3">
              <label for="Email Address">
                {{ $t("login.email") }}
                <font color="red">*</font>
              </label>
              <input
                type="email"
                class="form-control"
                id="Email Address"
                placeholder="Enter email"
                v-model="user.email"
                name="Email Address"
                v-validate="'required|email'"
                :class="{'is-invalid': errors.has('Email Address')}"
              >
              <span
                class="text-danger"
                v-if="errors.has('Email Address')"
              >{{errors.first('Email Address')}}</span>
            </div>
            <div class="form-group">
              <label for="password">
                {{ $t("login.password") }}
                <font color="red">*</font>
              </label>
              <input
                type="password"
                class="form-control"
                id="password"
                placeholder="Password"
                v-model="user.password"
                v-validate="'required'"
                :class="{'is-invalid': errors.has('password'),}"
                name="password"
              >
              <span class="text-danger" v-if="errors.has('password')">{{errors.first('password')}}</span>
            </div>
            <div class="form-group form-check">
              <input
                type="checkbox"
                class="form-check-input"
                id="exampleCheck1"
                v-model="checkboxremember"
                @change="Remember"
              >
              <label
                class="form-check-label"
                for="exampleCheck1"
              >{{ $t("login.remember_password") }}</label>
            </div>
            <div class="form-group">
              <small>
                <a
                  href="#"
                  class="form-check-label text-secondary"
                >{{ $t("login.forgot_password") }}</a>
              </small>
            </div>
            <button type="submit" class="primary-btn w-25">{{ $t("login.login") }}</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test " },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image"
      }
    ],
    title: "Greenvalley Log In"
  },
  data() {
    return {
      checkboxremember: null,
      user: {
        email: "",
        password: ""
      },
      res: {}
    };
  },
  methods: {
    //
    signin() {
      const api = `${process.env.VUE_APP_API}/auth/login`;
      const vm = this;
      console.log(api);
      // api 
      this.$validator.validate().then(result => {
        if (result) {
          this.$http.post(api, vm.user).then(response => {
            console.log(response.data);
            if (response.data.status) {
              console.log("");
              localStorage.setItem("token", response.data.token);
              localStorage.setItem("remember", vm.user.email);
              vm.res = response.data;
              vm.$router.push("/");
              this.message(this.$i18n.t("messages.sign_s"), "success");
            } else {
              vm.res = response.data;
              this.message(this.$i18n.t("messages.sign_f"), "warning");
              console.log(" -------------");
            }
          });
        } else {
          this.message(this.$i18n.t("messages.sign_f"), "warning");
          console.log(" ");
        }
      });
    },
    Remember() {
      const vm = this;
      if (vm.checkboxremember != "") {
        localStorage.setItem("remember", vm.user.email);
      } else {
        localStorage.setItem("remember", "");
      }
    }
  },
  created() {
    const vm = this;
    const name = localStorage.getItem("remember");
    if (name === "") {
      vm.checkboxremember = false;
    } else {
      vm.checkboxremember = true;
    }
    vm.user.email = name;
  }
};
</script>


