                    <p>5, We acknowledge that for the purposes of the Personal Property Securities Act 2009, these terms and conditions constitute a security agreement which creates a purchase money security interest in favour of Greenvalley in respect of all present and after acquired goods(and their proceeds) supplied to us by Greenvalley. We will do whatever is necessary in order to give a valid security interest over the goods which is able to be registered by Greenvalley on the Personal Properties Security Register.</p>
                    <p>6, We will not charge the goods in any way or grant or otherwise give any interest in the goods while they remain the property of Greenvalley, nor allow any third party to acquire a security interest in the goods.</p>
                    <p>7, No claim of any kind shall be entertained by Greenvalley after seven(7) days from the date of delivery.</p>
                    <p>8, Acknowledgement/Signature on the invoice of goods is proof of delivery.</p>
                    <p>9, I/We agree to be responsible to Greenvalley for all balances which are sue or may be from the time hereafter owned by the applicant, for goods and services supplied by Greenvalley to the applicant. Interest on the applicant's overdue payments, costs incurred by Greenvalley in the course of recovering and/or enforcing judgments for monies due and payable by the applicant are to be fully borne by the applicant.</p>
                    <p>PRIVACY stateMENT: Greenvalley is committed to protecting your privacy and the confidentiality and security of personal information provided by you to us. The information you provide is necessary to process your application and will be stored as record and profile information. The information provided by you will be used to contact you, your banker, and your referees. The information in this application form will be furnished to credit organizations for credit checks, and in the event of your default and non compliance with the default notice, may ne disclosed to a debt collection agency, and the overdue account published with a credit reporting agency. please contact Greenvalley's office is you would like to access or update information we hold about you or your company.</p>
                    <p>AUTHORITY IN RELATION TO CREDIT INFORMATION: I/We authorise Greenvalley to obtain a credit report containing information about me/us from a credit reporting agency, and any for commercial credit. I/We authorise Greenvalley to disclose my/our credit information to other credit providers and credit reporting agencies. I/We authorise Greenvalley to disclose my/our credit information to its collecting agents including debit collection agencies and its solicitors to enable collection of my/our overdue payments. I/We authorise Greenvalley to notify a credit reporting agency if I/We become overdue in making payments and list me/us in default.</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="form-group">
              <div class="form-check pl-0">
                <div class="custom-control custom-checkbox mb-3">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="customControlValidation1"
                    name="terms"
                    v-validate="'required'"
                    :class="{'is-invalid': errors.has('terms') }"
                  >
                  <label
                    class="custom-control-label"
                    for="customControlValidation1"
                  >{{$t('register.check_terms')}}</label>
                  <br>
                  <span class="text-danger" v-if="errors.has('terms')">{{errors.first('terms')}}</span>
                </div>
              </div>
            </div>
            <button class="primary-btn" type="submit">{{$t('register.registered')}}</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "home",
  data() {
    return {
      register: {
        isaddress: false,
        isShow: true,
        isemail: false,
        checkemail: "",
        first_name: "",
        last_name: "",
        birth: "",
        email: "",
        mobile_number: "",
        password: "",
        sex: "Male",
        postcode: "",

        registered_company_name: "",
        business_name: "",
        nature_business: "",
        business_contact_number: "",
        abn: "",

        director: "",
        director_birth_date: "",
        driving_license_no: "",
        director_state: 0,
        director_personal_address: "",

        accounts_contact_person: "",
        accounts_contact_number: "",
        accounts_contact_email: "",

        delivery_contact_person: "",
        delivery_contact_number: "",
        delivery_contact_address: "",
        delivery_contact_address_2: "",
        delivery_contact_country: "Australia",
        delivery_contact_city: "",
        delivery_contact_state: 0,
        delivery_contact_postcode: "",

        office_address: "",
        office_address_2: "",
        office_country: "Australia",
        office_city: "",
        office_state: 0,
        office_postcode: ""
      }
    };
  },
  methods: {
    //
    Business_Register() {
      const api = `${process.env.VUE_APP_API}/user/register?`;
      const vm = this;
      this.$validator.validate().then(result => {
        if (result) {
          console.log(" " + JSON.stringify(vm.register));
          this.$http.post(api, vm.register).then(response => {
            console.log(response.data);
            if (response.data.status) {
              this.message(this.$i18n.t("messages.registered"), "success");
              vm.$router.push("/login");
            }
          });
        } else {
          console.log(" " + JSON.stringify(vm.register));
        }
      });
    },
    //
    checkEmail() {
      const api = `http://api.localhost/api/user/isRegister`;
      const vm = this;
      console.log("Email:" + vm.register.email);
      this.$http.post(api, { email: vm.register.email }).then(response => {
        console.log(response.data.isRepeat);

        if (response.data.isRepeat || this.register.email == "") {
          $(".isemailcolor").addClass("text-danger");
          this.register.checkemail = "EMAIL has been used";
          this.register.isemail = true;
        } else {
          $(".isemailcolor").addClass("text-success");
          this.register.checkemail = "EMAIL can be used";
          this.register.isemail = false;
        }
      });
    },
    //
    dateChangebirthday(date, val) {
      this.register.date = val;
    }
  },
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test " },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image"
      }
    ],
    title: "Greenvalley Registered"
  }
};
</script>

