<template>
  <div>
    <div class="list_title d-flex">
      <div class="row w-100">
        <div class="col-sm-8 col-md-8 col-lg-9 mr-auto">
          <h1 class="mb-0" v-if="getLang === 'en'" id="Product_page">{{goodList[id].enname}}</h1>
          <h1 class="mb-0" v-else id="Product_page">{{goodList[id].cnname}}</h1>
        </div>
        <div class="col-sm-4 col-md-4 col-lg-3 text-right pr-0">
          <a class="text-info" href="#">
            <font-awesome-icon class="mr-2" icon="heart" :style="{ color: '#e74c3c' }"/>Add to Wishlist
          </a>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-5">
        <div class="border mt-4">
          <swiper :options="swiperOptionTop" class="gallery-top" ref="swiperTop">
            <swiper-slide v-for="image in goodList" :key="image.id" class="slide-img">
              <img class="mt-4" :src="goodList[id].img" alt style=" max-height: 450px;">
            </swiper-slide>
            <div class="swiper-button-next" slot="button-next"></div>
            <div class="swiper-button-prev" slot="button-prev"></div>
          </swiper>
          <swiper :options="swiperOptionThumbs" class="gallery-thumbs my-4" ref="swiperThumbs">
            <swiper-slide v-for="image in goodList" :key="image.id" class="slide-img">
              <img :src="goodList[id].img" alt>
            </swiper-slide>
          </swiper>
        </div>
      </div>
      <div class="col-md-7">
        <div class="mt-4 border-bottom py-3" v-if="getLang === 'en'">{{goodList[id].encontent}}</div>
        <div class="mt-4 border-bottom py-3" v-else>{{goodList[id].zhcontent}}</div>
        <div class="mt-3 border-bottom pb-3">
          <div class="d-flex">
            <div class="d-flex align-items-center">
              <ul class="list-unstyled">
                <li>
                  Paste:
                  <span>3kg</span>
                </li>
              </ul>
            </div>
            <div class="ml-auto">
              <strong class="text-danger h3">{{ goodList[id].price | currency }}</strong>
              <br>
              <del
                class="text-secondary d-flex justify-content-end mr-2"
              >{{ goodList[id].delprice | currency }}</del>
            </div>
          </div>
        </div>
        <div class="mt-4 py-2 d-flex">
          <div class="btn-group ml-auto" role="group" aria-label="Basic example">
            <div class="input-group carts-input-group">
              <div class="input-group-prepend">
                <button
                  type="button"
                  class="btn rounded-0"
                  style="background:transparent;border-color:#EAF0ED;font-size:16px;"
                  @click="less"
                >
                  <font-awesome-icon icon="minus" style="font-size:16px;"/>
                </button>
              </div>
              <input
                type="tel"
                class="form-control text-center rounded-0"
                v-model="input_number_value"
                :input="handleBlur(input_number_value)"
                style="width:50px"
                @blur="checkNumber()"
                maxlength="2"
              >
              <div class="input-group-append">
                <button
                  type="button"
                  class="btn rounded-0"
                  style="background:transparent;border-color:#EAF0ED;font-size:16px;"
                  @click="add"
                >
                  <font-awesome-icon icon="plus" style="font-size:16px;"/>
                </button>
              </div>
            </div>
            <button
              class="primary-btn rounded-0"
              plain
              @click="addToCartPage(goodList[id]), messages()"
            >Add to Cart</button>
          </div>
        </div>
      </div>
      <!-- <div class="row mt-5 p-3">
        <div class="col-12">
          <h4>Tables Style</h4>
          <p>Suspendisse at placerat turpis. Duis luctus erat vel magna pharetra aliquet. Maecenas tincidunt feugiat ultricies. Phasellus et dui risus. Vestibulum adipiscing, eros quis lobortis dictum. Etiam mollis volutpat odio, id euismod justo gravida a. Aliquam erat volutpat. Phasellus faucibus venenatis lorem, vitae commodo elit pretium et. Duis rhoncus lobortis congue. Vestibulum et purus dui, vel porta lectus. Sed vulputate pulvinar adipiscing.</p>
          <ul>
            <li>She was walking to the mall.</li>
            <li>Ted might eat the cake.</li>
            <li>You must go right now.</li>
            <li>Words were spoken.</li>
            <li>The teacher is writing a report.</li>
          </ul>
          <p>Here are some verb phrase examples where the verb phrase is the predicate of a sentence. In this case, the verb phrase consists of the main verb plus any auxiliary, or helping, verbs. Nulla nec velit. Mauris pulvinar erat non massa. Suspendisse tortor turpis, porta nec, tempus vitae, iaculis semper, pede.</p>
          <ol>
            <li>Shipping &amp; Delivery.</li>
            <li>Privacy &amp; Security.</li>
            <li>Returns &amp; Replacements.</li>
            <li>Payment, Pricing &amp; Promotions.</li>
            <li>Viewing Orders.</li>
          </ol>
          <p>Next are some verb phrase examples of verb phrases where the phrase has a single function which means it can act like an adverb or an adjective. The phrase would include the verb and any modifiers, complements, or objects. Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Morbi luctus. Duis lobortis. Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur nec posuere odio. Proin vel ultrices erat.</p>
        </div>
      </div>-->
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import "../../../../node_modules/swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  computed: {
    ...mapGetters(["goodList", "getLang"])
  },
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      max_Number: 99,
      input_number_value: 1,
      id: null,
      //
      swiperOptionTop: {
        initialSlide: 0,
        spaceBetween: 20,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      },
      //2
      swiperOptionThumbs: {
        initialSlide: 0,
        spaceBetween: 20,
        centeredSlides: true,
        slidesPerView: "auto",
        touchRatio: 0.2,
        slideToClickedSlide: true
      }
    };
  },
  methods: {
    ...mapActions(["addToCartPage"]),
    messages() {
      this.message(this.$i18n.t("messages.cart"), "success");
      this.init();
    },
    init() {
      this.input_number_value = 1;
    },
    handleBlur(value) {
      this.input_number_value = value;
      this.goodList[this.id].num = this.input_number_value;
    },
    //
    handleChange() {
      this.goodList[this.id].num = this.input_number_value;
    },
    less() {
      if (this.input_number_value === 1) return;
      else this.input_number_value--;
    },
    add() {
      if (this.input_number_value < this.max_Number) this.input_number_value++;
    },
    checkNumber() {
      if (this.input_number_value >= this.max_Number) {
        this.input_number_value = this.max_Number;
      } else if (this.input_number_value == 0) {
        this.input_number_value = 1;
      }
    }
  },
  created() {
    const _id = this.$route.params.id - 1;
    this.id = _id;
  },
  //
  mounted() {
    if (document.body.clientWidth < 767) {
      const Product_page = document.querySelector("#Product_page");
      Product_page.scrollIntoView(true);
    }

    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.swiper;
      const swiperThumbs = this.$refs.swiperThumbs.swiper;
      swiperTop.controller.control = swiperThumbs;
      swiperThumbs.controller.control = swiperTop;
    });
  }
};
</script>

<style lang="scss" scoped>
.gallery-top {
  height: 64% !important;
  width: 80%;
}
.gallery-thumbs {
  height: 20% !important;
  width: 80%;
}
.gallery-thumbs .swiper-slide {
  width: 100px;
  height: 100px;
  opacity: 0.4;
}
.gallery-thumbs .swiper-slide-active {
  opacity: 1;
}
img {
  width: inherit;
  height: inherit;
}
.swiper-button-prev {
  background-image: url("data:image/svg+xml;charset=utf-8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 27 44'><path d='M0,22L22,0l2.1,2.1L4.2,22l19.9,19.9L22,44L0,22L0,22L0,22z' fill='%23E78127'/></svg>") !important;
}

.swiper-button-next {
  background-image: url("data:image/svg+xml;charset=utf-8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 27 44'><path d='M27,22L27,22L5,44l-2.1-2.1L22.8,22L2.9,2.1L5,0L27,22L27,22z' fill='%23E78127'/></svg>") !important;
}
#product {
  width: 400px;
}
</style>

