<template>
  <div class="container">
    <h1 class="text-center my-5">Payment</h1>
    <Steps :Next="active" />
    <div class="row justify-content-center">
      <div class="col-md-8 my-5">
        <router-view></router-view>
        <div class="d-flex justify-content-center mt-3">
          <button class="primary-btn btn-block" @click="Next">Next</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Steps from "./Steps.vue";
export default {
  components: {
    Steps
  },
  data() {
    return {
      active: 1
    };
  },
  methods: {
    Next() {
      if (this.active === 1) {
        this.active = 2;
        this.$router.push("/payment");
      }
    }
  }
};
</script>
