<template>
  <div>
    <div class="text-center">
      <h2>Manage delivery addresses</h2>
      <small>Modify an existing address or add a new address</small>
      <br />
      <button
        class="primary-btn mt-4"
        data-toggle="modal"
        data-target="#newaddressModel"
        @click="openModel(true)"
      >
        <font-awesome-icon icon="plus" class="mr-2" />Add new address
      </button>
    </div>

    <!-- Modal -->
    <div
      class="modal fade"
      id="newaddressModel"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header border-bottom-0">
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <h4 class="text-center" v-if="this.isnew == true">
              Add new address
            </h4>
            <h4 class="text-center" v-else>Edit Address</h4>
            <div class="form-row">
              <div class="col-sm-12 mb-3">
                <label for="first_name">Address Type</label>
                <font color="red">*</font>
                <select
                  class="custom-select mr-sm-2"
                  id="director_informstion_state"
                >
                  <option selected value="DI">Delivery & Invoice</option>
                  <option value="D">Delivery</option>
                  <option value="I">Invoice</option>
                </select>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-12 mb-3">
                <label for="Building / Level / Suite"
                  >Building / Level / Suite</label
                >
                <input
                  type="text"
                  class="form-control"
                  id="Building / Level / Suite"
                  name="Building / Level / Suite"
                />
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-12 mb-3">
                <label for="Street Address">Street Address</label>
                <font color="red">*</font>
                <input
                  type="text"
                  class="form-control"
                  id="Street Address"
                  name="Street Address"
                  v-validate="'required'"
                  :class="{ 'is-invalid': errors.has('Street Address') }"
                />
                <span class="text-danger" v-if="errors.has('Street Address')">{{
                  errors.first("Street Address")
                }}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <label for="Postcode">Postcode</label>
                <font color="red">*</font>
                <input
                  type="tel"
                  class="form-control"
                  id="Postcode"
                  name="Postcode"
                  maxlength="4"
                  v-validate="'numeric|required|min:4'"
                  :class="{ 'is-invalid': errors.has('Postcode') }"
                />
                <span class="text-danger" v-if="errors.has('Postcode')">{{
                  errors.first("Postcode")
                }}</span>
              </div>
              <div class="col-sm-6 mb-3">
                <label for="City / Suburb">City / Suburb</label>
                <font color="red">*</font>
                <input
                  type="tel"
                  class="form-control"
                  id="City / Suburb"
                  name="City / Suburb"
                  v-validate="'required'"
                  :class="{ 'is-invalid': errors.has('City / Suburb') }"
                />
                <span class="text-danger" v-if="errors.has('City / Suburb')">{{
                  errors.first("City / Suburb")
                }}</span>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <label for="State">State</label>
                <font color="red">*</font>
                <select
                  v-if="this.isnew == true"
                  class="custom-select mr-sm-2"
                  id="State"
                >
                  <option selected>NSW</option>
                  <option value="ACT">ACT</option>
                  <option value="WA">WA</option>
                  <option value="QLD">QLD</option>
                  <option value="SA">SA</option>
                  <option value="VIC">VIC</option>
                  <option value="TAS">TAS</option>
                </select>
                <select
                  v-else
                  class="custom-select mr-sm-2"
                  id="State"
                  disabled
                >
                  <option selected>NSW</option>
                  <option value="ACT">ACT</option>
                  <option value="WA">WA</option>
                  <option value="QLD">QLD</option>
                  <option value="SA">SA</option>
                  <option value="VIC">VIC</option>
                  <option value="TAS">TAS</option>
                </select>
              </div>
            </div>
            <div class="form-row">
              <div class="col-sm-6 mb-3">
                <strong class="pl-0">Set as default</strong>
                <div class="custom-control custom-checkbox my-3">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="customCheck1"
                  />
                  <label class="custom-control-label" for="customCheck1"
                    >Delivery Address</label
                  >
                </div>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="customCheck2"
                  />
                  <label class="custom-control-label" for="customCheck2"
                    >Invoice Address</label
                  >
                </div>
              </div>
            </div>
          </div>

          <div class="modal-footer border-top-0">
            <button
              type="button"
              class="btn btn-secondary mr-auto"
              data-dismiss="modal"
            >
              Close
            </button>
            <button type="button" class="primary-btn">
              <font-awesome-icon icon="plus" class="mr-2" />Add address
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <div class="table-responsive-xl">
      <table class="table mt-5 table-hover">
        <thead>
          <tr>
            <th style="min-width:320px">Address</th>
            <th style="min-width:150px">Address Type</th>
            <th style="min-width:150px">Default</th>
            <th class="text-right">Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>124 Maple Road, NORTH ST MARYS NSW 2760</td>
            <td>Delivery & Invoice</td>
            <td>Delivery & Invoice</td>
            <td class="text-right">
              <button
                class="btn btn-outline-primary btm-sm mr-1"
                data-toggle="modal"
                data-target="#newaddressModel"
                @click="openModel(false)"
              >
                Edit
              </button>
            </td>
          </tr>
          <tr>
            <td colspan="4">
              <strong class="pl-0">Alternate Addresses</strong>
              <p>
                <small class="text-muted"
                  >Alternative delivery and invoice addresses used for
                  ordering</small
                >
              </p>
            </td>
          </tr>
          <tr>
            <td>15a St Marys Rd, BERKSHIRE PARK NSW 2765</td>
            <td>Delivery & Invoice</td>
            <td>Delivery Address</td>
            <td>
              <div class="btn-group" role="group" aria-label="Basic example">
                <button
                  type="button"
                  class="btn btn-outline-primary mr-1"
                  @click="openModel(false)"
                  data-toggle="modal"
                  data-target="#newaddressModel"
                >
                  Edit
                </button>
                <button type="button" class="btn btn-outline-danger">
                  Remove
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "Delivery_addresses",
  data() {
    return {
      isnew: false
    };
  },
  methods: {
    openModel(isnew) {
      if (isnew) {
        this.isnew = true;
      } else {
        this.isnew = false;
      }
      $("#productModal").modal("show");
    }
  },
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test " },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image"
      }
    ],
    title: "Manage delivery addresses",
    // override the parent template and just use the above title only
    titleTemplate: null
  }
};
</script>
