<template>
  <header id="header">
    <vm-back-top :height="1" :right="5" :bottom="55"></vm-back-top>
    <div class="bg-light">
      <nav
        class="container navbar-md navbar-light d-flex justify-content-end language py-0"
      >
        <div class="d-flex justify-content-end d-block">
          <ul class="list-inline mb-0">
            <li class="list-inline-item">
              <a
                class="navbar-brand mr-3 text-center"
                :class="{ 'router-link-active': lang }"
                href="#"
                @click.prevent="english"
                >English</a
              >
            </li>
            <li class="list-inline-item">
              <a
                class="navbar-brand mr-0 text-center"
                :class="{ 'router-link-active': !lang }"
                href="#"
                @click.prevent="chinese"
                ></a
              >
            </li>
          </ul>
        </div>
      </nav>
    </div>

    <div class="container my-5">
      <div class="row">
        <div class="col-md-4">
          <img class="img-fluid" src="../../static/images/Logo1.png" alt />
          <router-link
            v-if="totalNum != 0"
            class="nav-link text-secondary mobile_cart d-none"
            :to="{ path: '/shopping-cart' }"
          >
            <el-badge :value="totalNum" class="item">
              <font-awesome-icon icon="cart-arrow-down" class="mr-2" />
            </el-badge>
          </router-link>
        </div>
        <div class="col-md-8 navbar_list">
          <ul class="nav nav-pills d-flex justify-content-end">
            <li class="nav-item">
              <router-link
                class="nav-link text-secondary"
                :to="{ path: '/personal' }"
              >
                <font-awesome-icon icon="money-check-alt" class="mr-2" />
                {{ $t("header.account") }}
              </router-link>
            </li>
            <li class="nav-item">
              <router-link
                class="nav-link text-secondary"
                :to="{ path: '/login' }"
                v-if="islogin"
              >
                <font-awesome-icon icon="sign-in-alt" class="mr-2" />
                {{ $t("header.login") }}
              </router-link>
            </li>
            <li class="nav-item">
              <router-link
                class="nav-link text-secondary"
                :to="{ path: '/registered' }"
                v-if="islogin"
              >
                <font-awesome-icon icon="user-plus" class="mr-2" />
                {{ $t("header.SignUp") }}
              </router-link>
            </li>
            <li class="nav-item">
              <a
                class="nav-link text-secondary"
                v-if="!islogin"
                @click.prevent="logout"
              >
                <font-awesome-icon icon="sign-out-alt" class="mr-2" />
                {{ $t("header.signout") }}
              </a>
            </li>
            <li class="nav-item" v-if="totalNum == 0">
              <router-link
                class="nav-link text-secondary"
                :to="{ path: '/shopping-cart' }"
              >
                <font-awesome-icon icon="cart-arrow-down" class="mr-2" />
                {{ $t("header.cart") }}
              </router-link>
            </li>
            <li class="nav-item" v-else>
              <router-link
                class="nav-link text-secondary cart-item"
                :to="{ path: '/shopping-cart' }"
              >
                <el-badge :value="totalNum" class="item">
                  <font-awesome-icon icon="cart-arrow-down" class="mr-2" />
                  {{ $t("header.cart") }}
                </el-badge>
                <div class="list mt-4 p-2 overflow">
                  <h6 class="ml-3 float-sm-left text-info">
                    {{ $t("cart.select") }}
                  </h6>
                  <table class="table">
                    <tbody>
                      <tr v-for="item in cartProductsList" :key="item.id">
                        <td class="align-middle text-center">
                          <a
                            href="#"
                            class="text-muted"
                            @click="dialogVisibleTrue(item)"
                          >
                            <font-awesome-icon
                              icon="trash-alt"
                              class="text-danger"
                            />
                          </a>
                        </td>
                        <td class="align-middle" v-if="getLang === 'cn'">
                          {{ item.cnname }}
                        </td>
                        <td class="align-middle" v-else>{{ item.enname }}</td>
                        <td class="align-middle">{{ item.num }}</td>
                        <td class="align-middle w-25">
                          {{ item.total_num | currency }}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div class="list test p-3">
                  <div class="my-3 h4">
                    {{ $t("cart.total") }}  {{ totalPrice | currency }}
                  </div>
                  <button class="primary-btn btn-block" @click="clearAllCart">
                    <font-awesome-icon icon="trash-alt" class="mr-2" />
                    {{ $t("cart.clear") }}
                  </button>
                  <button class="primary-btn btn-block">
                    <font-awesome-icon icon="cart-arrow-down" class="mr-2" />
                    {{ $t("cart.checkout") }}
                  </button>
                </div>
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="container">
      <nav class="navbar navbar-expand-lg navbar-light bg-light menu_list px-0">
        <a class="navbar-brand p-2">Greenvalley</a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav d-flex justify-content-around w-100">
            <li class="nav-item w-100 text-center">
              <router-link class="nav-link" to="/" exact>{{
                $t("header_menu.home")
              }}</router-link>
            </li>
            <li class="nav-item w-100 text-center">
              <router-link class="nav-link" to="/products">{{
                $t("header_menu.products")
              }}</router-link>
            </li>
            <li class="nav-item w-100 text-center">
              <router-link class="nav-link" to="/about">{{
                $t("header_menu.about")
              }}</router-link>
            </li>
            <li class="nav-item w-100 text-center">
              <router-link
                class="nav-link"
                href="#"
                :to="{ path: '/contact' }"
                >{{ $t("header_menu.contact") }}</router-link
              >
            </li>
            <!-- <li class="nav-item w-100 text-center">
              <a class="nav-link" href="#">{{ $t("header_menu.career") }}</a>
            </li>-->
            <!-- <li class="nav-item w-100 text-center">
              <router-link class="nav-link" :to="{path: '/backstage/users'}"></router-link>
            </li>-->
            <li class="nav-item w-100 text-center private-menu d-none">
              <router-link
                class="nav-link text-secondary"
                :to="{ path: '/personal' }"
                v-if="!islogin"
                >{{ $t("header.account") }}</router-link
              >
            </li>
            <li class="nav-item w-100 text-center private-menu d-none">
              <router-link
                class="nav-link text-secondary"
                :to="{ path: '/login' }"
                v-if="islogin"
                >{{ $t("header.login") }}</router-link
              >
            </li>
            <li class="nav-item w-100 text-center private-menu d-none">
              <router-link
                class="nav-link text-secondary"
                :to="{ path: '/Business' }"
                v-if="islogin"
                >{{ $t("header.SignUp") }}</router-link
              >
            </li>
            <li class="nav-item w-100 text-center private-menu d-none">
              <a
                @click.prevent="logout"
                class="nav-link text-secondary"
                v-if="!islogin"
                >{{ $t("header.signout") }}</a
              >
            </li>
          </ul>
        </div>
      </nav>
    </div>
  </header>
</template>

<script>
import en from "vee-validate/dist/locale/en";
import cn from "vee-validate/dist/locale/zh_CN";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Header",
  data() {
    return {
      islogin: true,
      lang: null,
      dialogVisible: false,
      result: null
    };
  },
  methods: {
    ...mapActions(["updateLang", "delProduct", "clearAllCart"]),
    //
    english() {
      this.$i18n.locale = "en";
      this.$validator.localize("en", en);
      this.updateLang("en");
      this.lang = true;
    },
    //
    chinese() {
      this.$i18n.locale = "cn";
      this.$validator.localize("zh_CN", cn);
      this.updateLang("cn");
      this.lang = false;
    },
    //
    logout() {
      const api = `http://api.localhost/api/auth/logout`;
      const vm = this;
      this.$http.post(api).then(response => {
        console.log(response.data);
        if (response.data.status) {
          localStorage.setItem("token", "");
          this.message("Logged out", "success");
          vm.$router.push("/login");
        }
      });
    },
    dialogVisibleTrue(data) {
      this.dialogVisible = true;
      this.result = data;
      this.delProduct(this.result);
      this.dialogVisible = false;
    }
  },
  computed: {
    //
    ...mapGetters(["totalNum", "totalPrice", "cartProductsList", "getLang"])
  },
  created() {
    let initlang = window.localStorage.getItem("UserLang");
    if (initlang === "en") {
      this.lang = true;
    } else {
      this.lang = false;
    }
    if (initlang === null) this.english();
    // Menu
    let token = window.localStorage.getItem("token");
    if (!token || token === null) {
      this.islogin = true;
    } else {
      this.islogin = false;
    }
  }
};
</script>

<style>
.cart-item {
  text-align: center;
  position: relative;
}

.list {
  background-color: #f8f9fa;
  width: 350px;
  height: 300px;
  overflow-y: auto;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
  right: 0;
  position: absolute;
  transition: 1s all;
  transform: scale(0);
  transform-origin: 100% 0%;
  z-index: 99;
  cursor: context-menu;
}

.cart-item:hover > .list {
  transform: scale(1);
}

.test {
  width: 350px;
  margin-top: 320px;
  height: 180px;
}
</style>

