<template>
  <div>
    <section class="container mt-4">
      <h1 class="mt-5 border-bottom pb-3">{{ $t("cart.cart_title") }}</h1>
      <div class="row">
        <div class="col-lg-8">
          <div
            class="cart-row d-flex flex-column flex-md-row py-3 border-bottom"
            v-for="item in cartProductsList"
            :key="item.id"
          >
            <div class="d-flex align-items-center flex-grow-1 mr-md-3">
              <div class="my-4 mr-4 flex-grow-1 flex-md-grow-0">
                <div class="cart-image">
                  <img :src="item.img" class="cart_img" />
                </div>
              </div>
              <div
                class="d-flex align-items-md-center flex-column flex-md-row flex-md-grow-1"
              >
                <div class="flex-fill mr-md-4 cart_title">
                  <span class="h3" v-if="getLang === 'cn'">{{
                    item.cnname
                  }}</span>
                  <span class="h3" v-if="getLang === 'en'">{{
                    item.enname
                  }}</span>
                  <br />
                  <span class="h3 text-danger cart_price">{{
                    item.price | currency
                  }}</span>
                </div>
                <div
                  class="cart-control ml-md-auto mt-3 mt-md-0 input_number_block"
                >
                  <div class="input-group carts-input-group">
                    <div class="input-group-prepend">
                      <button
                        type="button"
                        class="btn rounded-0 input_number_btn"
                        @click="cart_less(item)"
                      >
                        <font-awesome-icon icon="minus" />
                      </button>
                    </div>
                    <input
                      :id="`cart_Number${item.id}`"
                      name="cart_Number"
                      type="tel"
                      class="form-control text-center rounded-0 input_number_input"
                      @change="handleChange(item)"
                      :value="item.num"
                      @blur="change_Blue_Number(item)"
                      maxlength="2"
                      onkeyup="this.value=this.value.replace(/\D/g,'')"
                    />
                    <div class="input-group-append">
                      <button
                        type="button"
                        class=" btn rounded-0 input_number_btn"
                        @click="cart_add(item)"
                      >
                        <font-awesome-icon icon="plus" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div
              class="price ml-md-auto align-items-center py-3 justify-content-end d-flex"
            >
              <span class="h5 mb-0 mr-md-3">{{
                item.total_num | currency
              }}</span>
              <a href="#" class="d-md-block" @click="dialogVisibleTrue(item)">
                <span class="text-danger">
                  <font-awesome-icon icon="trash-alt" />
                </span>
              </a>
            </div>
          </div>
        </div>
        <div class="col-lg-4">
          <div class="p-3 mt-5 order_summary_bg">
            <h4 class="text-center border-bottom pb-3">
              {{ $t("cart.order") }}
            </h4>

            <div class="d-flex justify-content-between mb-2">
              <span class="h4">{{ $t("cart.number") }}</span>
              <span class="h4">{{ totalNum }}</span>
            </div>
            <div class="d-flex justify-content-between mb-2">
              <span class="h4">{{ $t("cart.total") }}</span>
              <span class="h4">{{ totalPrice | currency }}</span>
            </div>
          </div>
          <div class="negative-row-margin mx-md-0">
            <button
              class=" primary-btn btn-block btn-lg py-3 mt-2 text-dank rounded-0"
              @click="clearAllCart"
              v-if="totalNum != 0"
            >
              {{ $t("cart.clear") }}
            </button>
            <router-link
              :to="{ path: '/information' }"
              href="#"
              class=" primary-btn btn-block btn-lg py-3 text-dank rounded-0"
              >{{ $t("cart.checkout") }}</router-link
            >
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Shopping_cart",
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test " },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image",
        content: ""
      }
    ],
    title: "Greenvalley Cart",
    // override the parent template and just use the above title only
    titleTemplate: null
  },
  data() {
    return {
      dialogVisible: false,
      result: null,
      input_number_value: 1,
      max_Number: 99
    };
  },
  computed: {
    ...mapGetters([
      "cartProducts",
      "totalNum",
      "totalPrice",
      "cartProductsList",
      "getLang"
    ])
  },
  methods: {
    ...mapActions([
      "delProduct",
      "numChange",
      "cart_add",
      "cart_less",
      "blue_number",
      "clearAllCart"
    ]),
    dialogVisibleTrue(data) {
      this.dialogVisible = true;
      this.result = data;
      this.delProduct(this.result);
      this.dialogVisible = false;
    },
    handleChange(data) {
      data.value = parseInt(this.input_number_value);
      this.numChange(data);
    },
    change_Blue_Number(data) {
      //
      let cart_Number_input = document.querySelector(`#cart_Number${data.id}`);
      //99
      if (cart_Number_input.value >= this.max_Number) {
        cart_Number_input.value = this.max_Number;
      } else if (cart_Number_input.value == 0) {
        return (cart_Number_input.value = 1);
      }
      data.num = parseInt(cart_Number_input.value);
      this.$store.dispatch("blue_number", data);
    }
  }
};
</script>

<style lang="scss" scoped>
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}
@media (max-width: 767.98px) {
  .price {
    border-top: solid 1px #eaf0ed;
  }
}
</style>
