<template>
  <div>
    <h3 class="mt-3">Categories</h3>
    <div class="list-group my-3">
      <a
        href="#"
        v-for="item in getCategories"
        :key="item"
        @click.prevent="categoryItem(item)"
        :class="{ 'active': getSearchText === item}"
        class="list-group-item list-group-item-action border-right-0 border-left-0"
      >{{item}}</a>
      <a
        href="#"
        class="list-group-item list-group-item-action border-right-0 border-left-0"
        @click.prevent="categoryItem('')"
        v-if="getLang ==='en'"
        :class="{ 'active': getSearchText === ''}"
      >ALL</a>
      <a
        href="#"
        class="list-group-item list-group-item-action border-right-0 border-left-0"
        @click.prevent="categoryItem('')"
        v-if="getLang ==='cn'"
        :class="{ 'active': getSearchText === ''}"
      ></a>
    </div>
    <!-- <div class="my-5">
      <h3 class="list_title">Price</h3>
      <div class="custom-control custom-checkbox ml-3">
        <input
          type="checkbox"
          class="custom-control-input"
          id="price1"
          v-model="price1"
          @change="test"
        >
        <label class="custom-control-label" for="price1">0.00 - $49.99</label>
      </div>
      <div class="custom-control custom-checkbox ml-3">
        <input type="checkbox" class="custom-control-input" id="customCheck1">
        <label class="custom-control-label" for="customCheck1">$50.00 - $99.99</label>
      </div>
      <div class="custom-control custom-checkbox ml-3">
        <input type="checkbox" class="custom-control-input" id="customCheck1">
        <label class="custom-control-label" for="customCheck1">0$100.00 and above</label>
      </div>
    </div>-->
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Sidebar",
  data() {
    return {
      categories: [],
      searchText: "",
      ahref: "#"
    };
  },
  computed: {
    ...mapGetters(["goodList", "getSearchText", "getSearchTextcn", "getLang"]),
    getCategories() {
      const vm = this;
      if (this.getLang === "en") {
        const encategory = new Set();
        vm.goodList.forEach(item => {
          encategory.add(item.encategory);
        });
        vm.categories = Array.from(encategory);
        this.a_init();
        return vm.categories;
      } else {
        const cncategory = new Set();
        vm.goodList.forEach(item => {
          cncategory.add(item.cncategory);
        });
        vm.categories = Array.from(cncategory);
        this.a_init();
        return vm.categories;
      }
    }
  },
  methods: {
    ...mapActions(["updateSearchTexten", "updateSearchTextcn"]),
    categoryItem(item) {
      const vm = this;
      if (item === "") {
        this.searchText = "";
        this.updateSearchTexten(item);
      }
      if (this.getLang === "en") {
        this.searchText = item;
        this.updateSearchTexten(item);
      } else {
        this.searchText = item;
        this.updateSearchTextcn(item);
      }
      this.a_init();
      vm.$router.push("/products");
    },
    a_init() {
      const vm = this;
      if (window.innerWidth <= 767) {
        vm.ahref = "#Products";
        setTimeout(() => {
          document.querySelector(vm.ahref).scrollIntoView(true);
        }, 200);
      } else {
        setTimeout(() => {
          vm.ahref = "#";
        }, 200);
      }
    }
  },
  created() {
    const vm = this;
    vm.a_init();
  }
};
</script>
