<template>
  <div>
    <div class="text-center mb-3">
      <h2>Edit details</h2>
      <small>Manage the details for your account</small>
    </div>
    <h5 class="my-3">Contact Information</h5>
    <div class="form-row">
      <div class="col-sm-6 mb-3">
        <label for="first_name">{{$t('register.first')}}</label>
        <font color="red">*</font>
        <input
          type="text"
          class="form-control"
          id="first_name"
          v-validate="'required'"
          name="First Name"
          :class="{'is-invalid': errors.has('First Name') }"
          v-model="tempProduct.first_name"
        >
        <span class="text-danger" v-if="errors.has('First Name')">{{errors.first('First Name')}}</span>
      </div>
      <div class="col-sm-6 mb-3">
        <label for="last_name">{{$t('register.last')}}</label>
        <font color="red">*</font>
        <input
          type="text"
          class="form-control"
          id="last_name"
          name="Last name"
          v-validate="'required'"
          :class="{'is-invalid': errors.has('Last name') }"
          v-model="tempProduct.last_name"
        >
        <span class="text-danger" v-if="errors.has('Last name')">{{errors.first('Last name')}}</span>
      </div>
    </div>
    <div class="form-row">
      <div class="col-sm-6 mb-3">
        <label for="birth">{{$t('register.birth')}}</label>
        <font color="red">*</font>
        <el-date-picker
          ref="picker"
          :editable="true"
          format="dd-MM-yyyy"
          value-format="dd-MM-yyyy"
          v-model="tempProduct.birth"
          type="date"
          id="birth"
          @change="dateChangebirthday"
          class="w-100"
          name="birth"
          v-validate="'required'"
        ></el-date-picker>
        <span class="text-danger" v-if="errors.has('birth')">{{errors.first('birth')}}</span>
      </div>
      <div class="col-sm-6 mb-3">
        <label for="Mobile number">{{$t('register.mobile')}}</label>
        <font color="red">*</font>
        <input
          type="tel"
          class="form-control"
          id="Mobile number"
          name="Mobile Number"
          v-validate="'required|length:12'"
          :class="{'is-invalid': errors.has('Mobile Number') }"
          v-model="tempProduct.mobile_number"
          v-mask="'## #### ####'"
        >
        <span
          class="text-danger"
          v-if="errors.has('Mobile Number')"
        >{{errors.first('Mobile Number')}}</span>
      </div>
    </div>
    <h5>Login Information</h5>
    <div class="form-row">
      <div class="col-sm-6 mb-3">
        <label for="email">{{$t('register.email')}}</label>
        <font color="red">*</font>
        <input
          type="email"
          class="form-control"
          id="email"
          name="Email Address"
          v-validate="'required|email'"
          :class="{'is-invalid': errors.has('Email Address') }"
          disabled
          v-model="tempProduct.email"
        >
        <span
          class="text-danger"
          v-if="errors.has('Email Address')"
        >{{errors.first('Email Address')}}</span>
      </div>
      <div class="col-sm-6 mb-3">
        <label for="Current Password">Current Password</label>
        <font color="red">*</font>
        <input
          type="text"
          class="form-control"
          id="Current Password"
          v-validate="'min:6|alpha_num'"
          :class="{'is-invalid': errors.has('Current Password') }"
          name="Current Password"
          minlength="6"
          v-model="password"
          @blur="checkPass()"
        >
        <span
          class="text-danger"
          v-if="errors.has('Current Password')"
        >{{errors.first('Current Password')}}</span>
        <div v-if="!errors.has('Current Password')" class="isemailcolor" v-text="checkpass"></div>
      </div>
    </div>
    <div class="form-row">
      <div class="col-sm-6 mb-3">
        <label for="password">Password</label>
        <font color="red">*</font>
        <input
          type="password"
          class="form-control"
          id="password"
          v-validate="'min:6|alpha_num'"
          :class="{'is-invalid': errors.has('password') }"
          name="password"
          minlength="6"
        >
        <span class="text-danger" v-if="errors.has('password')">{{errors.first('password')}}</span>
      </div>
      <div class="col-sm-6 mb-3">
        <label for="Confirm Password">Confirm Password</label>
        <font color="red">*</font>
        <input
          type="password"
          class="form-control"
          id="Confirm Password"
          v-validate="'min:6|alpha_num'"
          :class="{'is-invalid': errors.has('Confirm Password') }"
          name="Confirm Password"
          minlength="6"
        >
        <span
          class="text-danger"
          v-if="errors.has('Confirm Password')"
        >{{errors.first('Confirm Password')}}</span>
      </div>
    </div>
    <h5>Defaults</h5>
    <p>
      <strong class="pl-0">Delivery &amp; Invoice Address</strong>
      <br>
      <span>{{tempProduct.postcode}}</span>
    </p>
    <router-link
      tag="a"
      :to="{path: '/personal/Delivery_addresses'}"
      class="list-group-item"
    >{{$t('personal.manage_delivery_addresses')}}</router-link>
    <div class="my-5">
      <button class="primary-btn" @click="putPersonal">Save changes</button>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "Edit_details",
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test " },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image"
      }
    ],
    title: "Greenvalley Personal",
    // override the parent template and just use the above title only
    titleTemplate: null
  },
  data() {
    return {
      tempProduct: {},
      putpersonal: [],
      password: "",
      checkpass: ""
    };
  },
  methods: {
    dateChangebirthday(val) {
      console.log(this.tempProduct.birth);
      this.tempProduct.birth = val;
    },
    getPersonal() {
      const api = `${process.env.VUE_APP_API}/user/profile`;
      const vm = this;
      this.$validator.validate().then(result => {
        if (result) {
          this.$http.get(api).then(response => {
            console.log(response);
            if (response.data) {
              this.tempProduct = response.data;
              this.dateChangebirthday();
            }
          });
        } else {
          console.log(" " + JSON.stringify(vm.register));
        }
      });
    },
    putPersonal() {
      const api = `${process.env.VUE_APP_API}/user/profile`;
      const vm = this;
      this.$validator.validate().then(result => {
        if (result) {
          this.$http.put(api, vm.tempProduct).then(response => {
            console.log(response.data);

            if (response.data) {
              this.putpersonal = response.data;
              console.log(this.putpersonal);
              this.missage();
            }
          });
        } else {
          console.log(" " + JSON.stringify(vm.register));
        }
      });
    },
    checkPass() {
      const api = `${process.env.VUE_APP_API}/user/doubleCheck`;
      const vm = this;
      console.log("Pass:" + vm.password);
      this.$http.post(api, { email: vm.password }).then(response => {
        console.log(response.data);

        if (response.data.isPass || this.password == "") {
          $(".isemailcolor").removeClass("text-success");
          $(".isemailcolor").addClass("text-danger");
          this.checkpass = "wrong password";
        } else {
          $(".isemailcolor").removeClass("text-danger");
          $(".isemailcolor").addClass("text-success");
          this.checkpass = "The password is correct";
        }
      });
    },
    missage() {
      this.$notify({
        title: "Success",
        message: "Successfully modified",
        type: "success",
        offset: 150
      });
    }
  },
  created() {
    this.getPersonal();
  }
};
</script>
