`<template>
  <div>
    <h2 class="list_title mt-2" id="Products">Product List</h2>
    <div class="search">
      <input type="text" v-model="searchProduct" placeholder="Search">
    </div>
    <div class="row">
      <div class="col-12">
        <div v-for="item in filterDataen" :key="item.id" class="card-list shadow border my-3">
          <div class="row m-0">
            <div class="col-lg-2 col-md-4 col-sm-4 col-5 px-0 border-right">
              <img
                v-if="item.icon==1"
                class="sale"
                src="../../../static/images/sale.png"
                alt="Sale"
              >
              <img
                v-else-if="item.icon==2"
                class="sale"
                src="../../../static/images/new.png"
                alt="Sale"
              >
              <img
                v-else-if="item.icon==3"
                class="sale"
                src="../../../static/images/top.png"
                alt="Sale"
              >
              <div class="p-2 h-100">
                <router-link href="#" class="text-center" :to="{path: `/products/${item.id}`}">
                  <img class="prev h-100" alt="Responsive image" :src="item.img">
                </router-link>
              </div>
            </div>
            <div class="col-lg-8 col-md-5 col-sm-5 col-7 border-right p-2">
              <h6 class="my-2 text-truncate">
                <router-link
                  v-if="getLang === 'en'"
                  href="#"
                  class="text-center"
                  :to="{path:  `/products/${item.id}`}"
                >{{item.enname}}</router-link>
                <router-link
                  v-else
                  href="#"
                  class="text-center"
                  :to="{path:  `/products/${item.id}`}"
                >{{item.cnname}}</router-link>
              </h6>
              <truncate
                clamp="..."
                class="mb-2"
                :length="150"
                less
                v-if="getLang === 'en'"
                :text="item.encontent"
              ></truncate>
              <truncate clamp="..." class="mb-2" :length="90" less v-else :text="item.zhcontent"></truncate>
            </div>
            <div class="col-lg-2 col-md-3 col-sm-3 col-12 add_to_cart p-1">
              <div class="text-center my-2 add_to_cart_price mr-auto">
                <strong
                  class="text-danger h3 mb-0 d-flex align-items-center justify-content-center"
                >{{ item.price | currency }}</strong>
              </div>
              <div class="text-center add_to_cart_del_price">
                <del v-if="item.delprice!=0">{{ item.delprice | currency }}</del>
              </div>
              <div class="text-center add_to_cart_icon">
                <font-awesome-icon
                  class="love"
                  icon="heart"
                  :style="{ color: '#e74c3c' }"
                  size="2x"
                />
              </div>
              <div class="text-center cart_btn my-3">
                <button
                  class="primary-btn"
                  plain
                  @click="addToCart(item), messages()"
                >Add to Cart</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import truncate from "vue-truncate-collapsed";
export default {
  name: "Products_Grid_List",
  components: {
    truncate
  },
  metaInfo: {
    meta: [
      {
        property: "og:title",
        content: "Greenvalley"
      },
      { property: "og:description", content: "Test " },
      { property: "og:type", content: "Test website" },
      { property: "og:url", content: "localhost" },
      {
        property: "og:image",
        content: ""
      }
    ],
    title: "Greenvalley Products",
    // override the parent template and just use the above title only
    titleTemplate: null
  },
  data() {
    return {
      lang: "",
      searchText: "",
      searchProduct: "",
      categories: []
    };
  },
  computed: {
    ...mapGetters(["goodList", "getLang", "getSearchText"]),
    filterDataen() {
      const vm = this;
      let _search = vm.searchProduct;
      if (this.getLang === "en") {
        return vm.goodList.filter(item => {
          const data = item.encategory
            .toLowerCase()
            .includes(vm.getSearchText.toLowerCase());
          if (data === true) {
            return Object.keys(item).some(function(key) {
              return (
                String(item[key])
                  .toLowerCase()
                  .indexOf(_search.toLowerCase()) > -1
              );
            });
          }
          return data;
        });
      } else {
        if (this.getLang === "cn") {
          return vm.goodList.filter(item => {
            const data = item.cncategory
              .toLowerCase()
              .includes(vm.getSearchText.toLowerCase());
            if (data === true) {
              return Object.keys(item).some(function(key) {
                return (
                  String(item[key])
                    .toLowerCase()
                    .indexOf(_search.toLowerCase()) > -1
                );
              });
            }
            return data;
          });
        }
      }
      return this.goodList;
    }
  },
  methods: {
    ...mapActions(["addToCart"]),
    messages() {
      this.message(this.$i18n.t("messages.cart"), "success");
    }
  }
};
</script>

<style lang="scss" scoped>
.search {
  background-color: #59b7c2;
  box-shadow: 0 1px 1px #ccc;
  border-radius: 2px;
  padding: 14px;
  margin: 45px auto 20px;
  margin-top: 20px;
  position: relative;
}

.search input {
  background: #fff no-repeat 13px 13px;
  background-image: url(data:image/png;base64,);

  border: none;
  width: 100%;
  line-height: 19px;
  padding: 11px 0;

  border-radius: 2px;
  box-shadow: 0 2px 8px #c4c4c4 inset;
  text-align: left;
  font-size: 14px;
  font-family: inherit;
  color: #738289;
  font-weight: bold;
  outline: none;
  text-indent: 40px;
}
</style>
