import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import VueAxios from 'vue-axios'
import VeeValidate from 'vee-validate'
import Meta from 'vue-meta'
import {
  library
} from '@fortawesome/fontawesome-svg-core'
import {
  fas
} from '@fortawesome/free-solid-svg-icons'
import {
  far
} from '@fortawesome/free-regular-svg-icons'
import {
  fab
} from '@fortawesome/free-brands-svg-icons'
import {
  FontAwesomeIcon,
  FontAwesomeLayers,
  FontAwesomeLayersText
} from '@fortawesome/vue-fontawesome'
import VueCurrencyFilter from 'vue-currency-filter'
import 'bootstrap'
import VueI18n from 'vue-i18n'
import VueMask from 'v-mask'
import Element from 'element-ui'
import enLocale from 'element-ui/lib/locale/lang/en'
import zhLocale from 'element-ui/lib/locale/lang/zh-CN'
import 'element-ui/lib/theme-chalk/index.css'
import {
  Message
} from 'element-ui'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import VmBackTop from 'vue-multiple-back-top'
import Cleave from 'vue-cleave-component';

Vue.use(Cleave);

//GOTOP
Vue.component(VmBackTop.name, VmBackTop)

Vue.component('Loading', Loading);
//Element 
const message = Message

//global 
Vue.prototype.message = function (text, types) {
  message({
    message: text,
    type: types
  })
}

Vue.use(Element, {
  i18n: (key, value) => i18n.t(key, value)
})

//
Vue.use(VueMask)

//
Vue.use(VueCurrencyFilter, {
  symbol: '$',
  thousandsSeparator: '.',
  fractionCount: 2,
  fractionSeparator: ',',
  symbolPosition: 'front',
  symbolSpacing: true
})

Vue.config.productionTip = false
//vueajax
Vue.use(VueAxios, axios)
axios.defaults.timeout = 10000
axios.interceptors.request.use(
  config => {
    var token = localStorage.getItem('token');
    if (token) { // tokenhttp headertoken
      config.headers.Authorizatio = `${token}`;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  });

axios.interceptors.response.use(response => {
  return response;
}, error => {
  if (error.response) {
    switch (error.response.status) {
      case 401:
        /* 
           401 token
          token
          Vuextoken
        */
        router.push('/login')
    }
  }
  return Promise.reject(error.response.data) //
});


//
router.beforeEach((to, from, next) => {
  let token =
    localStorage.getItem('token') ||
    localStorage.setItem('token', '')
  if (to.meta.requiresAuth && !token || token === null) {
    router.push('/login')
    message({
      message: i18n.t('messages.login'),
      type: 'warning'
    })
    console.log('')
  } else {
    next()
  }
})

router.afterEach(() => {
  window.scrollTo(0, 0)
})

//Icons
library.add(fas, far, fab)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.component('font-awesome-layers', FontAwesomeLayers)
Vue.component('font-awesome-layers-text', FontAwesomeLayersText)

//
Vue.use(Meta)
Vue.use(Meta, {
  keyName: 'metaInfo', // the component option name that vue-meta looks for meta info on.
  attribute: 'data-vue-meta', // the attribute name vue-meta adds to the tags it observes
  ssrAttribute: 'data-vue-meta-server-rendered', // the attribute name that lets vue-meta know that meta info has already been server-rendered
  tagIDKeyName: 'vmid' // the property name that vue-meta uses to determine whether to overwrite or append a tag
})



//
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: store.state.currentLang, // 
  messages: {
    en: Object.assign(require('./locales/en'), enLocale),
    cn: Object.assign(require('./locales/cn'), zhLocale),
  },
})

//unFocus 
Vue.use(VeeValidate, {
  events: 'input|blur'
})

new Vue({
  router,
  store,
  i18n,
  'element-ui': 'ELEMENT',
  render: h => h(App),
  data() {
    return {
      baseUrl: process.env.BASE_URL
    }
  }
}).$mount('#app')