import Vue from 'vue'
import Router from 'vue-router'

import Shopping_cart from './view/Navbar/Shopping_cart.vue'
import Login from './view/Navbar/Login.vue'
import Registered from './view/Navbar/Registered.vue'

import Home from './view/Menu/Home.vue'
import About from './view/Menu/About.vue'
import Contact from './view/Menu/Contact.vue'

import Products from './components/product/Products.vue'
import Products_Grid_List from './view/Menu/Product/Products_Grid_List.vue'
import Products_page from './view/Menu/Product/Products_page.vue'

import Personal from './components/personal/Personal.vue'
import Edit_details from './view/Navbar/Personal/Edit_details.vue'
import Delivery_addresses from './view/Navbar/Personal/Delivery_addresses.vue'

import Payment_index from './components/Payment/index.vue'
import Order_Information from './view/Payment/Order_Information.vue'
import Payment from './view/Payment/Payment.vue'

import error from './view/404/404.vue'

Vue.use(Router)

export default new Router({
    // mode: 'history',
    routes: [
        {
            path: '*',
            component: error,
        },
        {
            path: '/',
            name: '',
            component: Home,
        },
        {
            path: '/shopping-cart',
            name: '',
            component: Shopping_cart,
        },
        {
            path: '/login',
            name: '',
            component: Login,
        },
        {
            path: '/registered',
            name: '',
            component: Registered,
        },
        {
            path: '/about',
            name: '',
            component: About,
        },
        {
            path: '/contact',
            name: '',
            component: Contact,
        },

        {
            path: '/personal',
            component: Personal,
            meta: {
                requiresAuth: false,
            },
            children: [
                {
                    path: '/',
                    component: Edit_details,
                },
                {
                    path: '/personal/Delivery_addresses',
                    component: Delivery_addresses,
                },
            ],
        },
        {
            path: '/products',
            component: Products,
            children: [
                {
                    path: '/products',
                    component: Products_Grid_List,
                },
                {
                    path: '/products/:id',
                    component: Products_page,
                },
            ],
        },
        {
            path: '/information',
            name: '',
            component: Payment_index,
            children: [
                {
                    path: '/information',
                    component: Order_Information,
                },
                {
                    path: '/Payment',
                    component: Payment,
                },
            ],
        },
    ],
})
